import { useState, useEffect, useCallback } from "react";
import { useParams, Link } from "react-router-dom";
import Moviepage from "../components/Moviepage";

function Detail() {
  const { id } = useParams();
  const [loading, setLoading] = useState(true);
  const [contMovie, setMovie] = useState([]);

  const getMovie = useCallback(async () => {
    const json = await (
      await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)
    ).json();
    setMovie(json);
    setLoading(false);
  }, [id]);

  useEffect(() => {
    getMovie();
  }, [getMovie]);

  console.log(contMovie);

  return (
    <div>
      {loading ? (
        <h2>Loading...</h2>
      ) : (
        <>
          <div>
            <Link to="/">Home</Link>
          </div>
          <div>
            {contMovie.status === "ok" ? (
              <Moviepage
                coverImg={contMovie.data.movie.medium_cover_image}
                title={contMovie.data.movie.title}
                summary={contMovie.data.movie.summary}
                genres={contMovie.data.movie.genres}
                year={contMovie.data.movie.year}
                runtime={contMovie.data.movie.runtime}
                description_full={contMovie.data.movie.description_full}
              />
            ) : (
              <div>Something wrong on load data</div>
            )}
          </div>
        </>
      )}
    </div>
  );
}
export default Detail;
